// mixin引入
import saleCommon from '../mixins/saleCommon'
export default {
  name: 'sale-Report',
  mixins: [saleCommon],
  methods: {
    // 前往合计
    goCount() {
      // 缓存
      const ROSE_OPTION = JSON.parse(localStorage.getItem("ROSE_OPTION")) || '';
      // 超过20:00 不允许提交
      if (!this.btnStatus) return;
      // 声明变量
      let { dept, region, bank, groupName, userCode } = ROSE_OPTION
      // 队长，省长，常委，备注
      let captain = this.helpCaptain, governor = this.governor,
          member = this.member, comments = this.comments;
      // 声明提示文字
      let tips = '';
      let tipArr = [
          { key: 'helpCaptain', msg: '队长信息'},
          { key: 'governor', msg: '省长信息'},
          { key: 'member', msg: '常委信息'},
          { key: 'Aclass', msg: 'A类产品销售金额'},
          { key: 'Bclass', msg: 'B类产品销售金额'},
          { key: 'arr', msg: '项目类产品'}
        ];
      tipArr.every( item => {
          if(!this[item.key] && this[item.key] !== 0){
            tips = `请填写${item.msg}`
            return;
          }  
          else if(item.key === 'arr' && this[item.key].length){
            for(let inx in this[item.key]){
              if(!this[item.key][inx].value.length){
                tips = `请选择${item.msg}`;
              }
              else if(this[item.key][inx].value[0] !== '无' && !this[item.key][inx].qty && this[item.key][inx].qty !== 0){
                tips = '请填写产品数量';
              }
            }
            return;
          }  
        return true;
      })
      if(tips){
        this.$vux.alert.show({
          content: tips
        })
        return;
      }
      // 提交表单内容
      let jsonData = {
        listId: "4bda3e47-a088-4749-a988-ebb07cfb00e4",
        referenceId: this.guid(),
        baseinfoExt: {
          id: this.guid(),
          varchar1: dept,
          varchar2: captain,
          varchar3: region,
          varchar4: bank,
          varchar5: groupName,
          varchar6: "否",
          varchar7: governor,
          varchar8: member,
          varchar9: comments,
          varchar12:userCode, //工号
        },
        transDetailUncalc: [
          { id: this.guid(),
            transObjCode: "A类产品", //项目类产品名称
            containerCode: "A", //类型
            qty: "",
            amount: Number(this.Aclass), //总金额
            fgCode: ""
          },
          { id: this.guid(),
            transObjCode: "B类产品", //项目类产品名称
            containerCode: "B", //类型
            qty: "",
            amount: Number(this.Bclass), //总金额
            fgCode: ""
          }
        ],
        transCode: "XHXSDD"
      };

      // 项目类产品
      for (let i = 0; i < this.arr.length; i++) {
        jsonData.transDetailUncalc.push({
          id: this.guid(),
          transObjCode: this.arr[i].value[0] === "无" ? "无" : this.arr[i].value[0].split("_")[0], //项目类产品名称
          containerCode: "项目类产品", //类型
          qty: this.arr[i].value[0] === "无" ? "" : this.arr[i].qty,
          taxAmount: this.arr[i].value[0] === "无" ? "" : Number(this.arr[i].value[0].split("_")[2]),
          amount: this.arr[i].qty * this.arr[i].value[0].split('_')[3], //总金额
          fgCode: ""
        });        
      }

      let totalInfo = {
        isMobile: true,
        conn: 20000,
        list: "trans_form_data",
        transCode: "XHXSDD",
        jsonData: JSON.stringify(jsonData)
      };
      this.totalInfo = totalInfo;
      // 提交表单内容 缓存
      localStorage.setItem(
        "saleReportInfo",
        JSON.stringify({
          saleReportRemark: totalInfo,
          time: new Date().getTime()
        })
      );
      // 用户 所属信息 缓存
      localStorage.setItem(
        "SALE_BASIC_INFO",
        JSON.stringify({
          member: member,             // 常委
          governor: governor,         // 省长
          captain: this.helpCaptain,  // 队长
          time: new Date().getTime()
        })
      );
      // 用户 表单详情 缓存
      localStorage.setItem(
        "SALE_FORM_INFO",
        JSON.stringify({
          Aclass: this.Aclass,        // A类产品金额
          Bclass: this.Bclass,        // B类产品金额
          saleReportArr: this.arr,    // 已选择的项目类产品
          comments: this.comments,    // 备注
          time: new Date().getTime()
        })
      );
      this.$router.push({ path: "/count" });
    }
  },
  created(){
    // 所属信息 缓存
    const SALE_FORM_INFO = JSON.parse(localStorage.getItem("SALE_FORM_INFO")) || '';       
    const SALE_BASIC_INFO = JSON.parse(localStorage.getItem("SALE_BASIC_INFO")) || '';
    // 回显表单内容
    this.echoStorage(SALE_BASIC_INFO, SALE_FORM_INFO);
  },
  beforeRouteLeave(to, from, next) {
    if (!this.arr[0].value.length || to.name === "Count") next();
    else {
      this.$vux.confirm.show({
        title: "温馨提示",
        content: "要保存数据吗？",
        confirmText: "确认",
        cancelText: "取消",
        onCancel: () => {
          // 仅删除 用户填写的表单内容
          localStorage.removeItem("SALE_FORM_INFO");
          next();
        },
        onConfirm: () => {
          localStorage.setItem(
            "SALE_FORM_INFO",
            JSON.stringify({
              Aclass: this.Aclass,
              Bclass: this.Bclass,
              saleReportArr: this.arr,
              comments: this.comments,
              time: new Date().getTime()
            })
          );
          // 用户 所属信息 缓存
          localStorage.setItem(
            "SALE_BASIC_INFO",
            JSON.stringify({
              member: this.member,             
              governor: this.governor,         
              captain: this.helpCaptain,
              time: new Date().getTime()
            })
          );
          next();
        }
      });
    }
  }
};



